import React, { useState, useMemo, useEffect, useCallback } from 'react';
import {
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, Cell,
  PieChart, Pie
} from 'recharts';
import AISidePanel from './AISidePanel'; // Import the new component
import Box from '@mui/material/Box';
import Fab from '@mui/material/Fab';
import AutoAwesomeIcon from '@mui/icons-material/AutoAwesome'; // Changed to sparkle icon

// Colors for the Pie Chart slices
const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#AF19FF', '#FF4560', '#775DD0', '#82ca9d', '#ffc658', '#d0ed57', '#a4de6c', '#8884d8'];
// Define the base URL for the backend API (same as in App.js)
const API_BASE_URL = 'http://localhost:3001/api';

const BudgetVisualization = ({ customBudgetData, customExpenseData, customActualsData }) => {
  // --- State Management ---
  const [viewMode, setViewMode] = useState('yearly');
  const [chartType, setChartType] = useState('bar');
  const [selectedMonth, setSelectedMonth] = useState('');
  const [drilldownCategory, setDrilldownCategory] = useState(null);
  const [currentPieData, setCurrentPieData] = useState([]);
  const [availableMonths, setAvailableMonths] = useState([]);

  // --- State for AI Overview (Existing) ---
  const [aiSummary, setAiSummary] = useState('');
  const [isSummaryLoading, setIsSummaryLoading] = useState(false);
  const [summaryError, setSummaryError] = useState(null);
  const [aiSummaryCache, setAiSummaryCache] = useState({}); // Existing AI Overview Cache

  // --- State for AI Insight Panel ---
  const [isAIPanelOpen, setIsAIPanelOpen] = useState(false);
  const [currentAIContext, setCurrentAIContext] = useState({ view: 'yearly', month: null });
  const [isRotating, setIsRotating] = useState(false); // State for rotation animation


  // --- Effects ---
  useEffect(() => {
    // Initialize available months and selected month
    if (customBudgetData?.months && customBudgetData.months.length > 0) {
      setAvailableMonths(customBudgetData.months);
      // Set default selected month only if it's not already set or invalid
      if (!selectedMonth || !customBudgetData.months.includes(selectedMonth)) {
        setSelectedMonth(customBudgetData.months[0]);
      }
    } else {
      setAvailableMonths([]);
      setSelectedMonth('');
    }
  }, [customBudgetData, selectedMonth]); // Depend only on customBudgetData

  // EFFECT 1: Clear AI Cache when underlying data changes
  useEffect(() => {
    console.log("Core data prop changed (budget, expense, or actuals), clearing AI summary cache.");
    setAiSummaryCache({});
    // We don't trigger a fetch here directly, the next effect will handle it if needed.
  }, [customBudgetData, customExpenseData, customActualsData]);

  // EFFECT 2: Fetch AI Summary when view/month changes (will use cache if available)
  useEffect(() => {
    const fetchAiSummary = async () => {
      // Guard against running if core data isn't ready for the current view
      // Note: Cache invalidation is handled by the effect above
      if (!customBudgetData || !customExpenseData || (viewMode === 'monthly' && !selectedMonth)) {
          console.log("Skipping AI summary fetch: Core data or selected month missing for current view.");
          setAiSummary(''); // Clear summary if context is invalid
          setSummaryError(null);
          setIsSummaryLoading(false);
          return;
      }

      // --- Cache Logic ---
      const cacheKey = viewMode === 'yearly' ? 'yearly' : `monthly-${selectedMonth}`;
      if (aiSummaryCache[cacheKey]) {
          console.log(`Cache hit for AI summary: ${cacheKey}`);
          setAiSummary(aiSummaryCache[cacheKey]);
          setIsSummaryLoading(false);
          setSummaryError(null);
          return; // Use cached version
      }

      // --- Fetch Logic (Cache Miss) ---
      console.log(`Cache miss for AI summary: ${cacheKey}. Fetching...`);
      setIsSummaryLoading(true);
      setSummaryError(null);
      setAiSummary(''); // Clear previous summary while loading

      let apiUrl = `${API_BASE_URL}/ai/overview?viewMode=${viewMode}`;
      if (viewMode === 'monthly' && selectedMonth) {
        apiUrl += `&month=${selectedMonth}`;
      }

      console.log(`Fetching AI summary from: ${apiUrl}`);

      try {
        const response = await fetch(apiUrl);
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || `HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        const fetchedSummary = data.summary || 'No summary available.';
        setAiSummary(fetchedSummary);
        // Store in cache
        setAiSummaryCache(prevCache => ({ ...prevCache, [cacheKey]: fetchedSummary }));
        console.log(`Stored AI summary in cache for key: ${cacheKey}`);
      } catch (error) {
        console.error('Error fetching AI summary:', error);
        setSummaryError(error.message);
        setAiSummary('');
      } finally {
        setIsSummaryLoading(false);
      }
    };

    fetchAiSummary();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [viewMode, selectedMonth, aiSummaryCache]); // DEPENDENCIES UPDATED: Fetch depends on view/month and checks cache state

  // EFFECT 3: Update AI Panel Context when viewMode or selectedMonth changes
  useEffect(() => {
    setCurrentAIContext({
      view: viewMode,
      month: viewMode === 'monthly' ? selectedMonth : null,
    });
  }, [viewMode, selectedMonth]);


  // --- Utility Functions ---
  const formatCurrency = useCallback((value) => {
    return new Intl.NumberFormat('en-ZA', {
      style: 'currency',
      currency: 'ZAR',
      maximumFractionDigits: 0
    }).format(value || 0);
  }, []);

  const isValidNumber = useCallback((value) => {
    // Check if value is defined, not null, and is a finite number
    return value !== undefined && value !== null && isFinite(value);
  }, []);

  // --- Data Processing Logic (Memoized) ---
  const processedData = useMemo(() => {
    console.log("Recalculating processedData..."); // Add log to see when this runs
    // Initial validation
    if (!customBudgetData || !customExpenseData) {
      console.warn("Budget or Anticipated Expense data missing in BudgetVisualization");
      return {
        error: "Essential budget or anticipated expense data is missing.",
        months: [], monthlyComparisonData: [], categoryData: [],
        monthlyExpensesByCategory: [], pieChartData: [],
        actualSubcategoryTotals: {},
        displayTotals: { budget: 0, comparison: 0, variance: 0, percentOfBudget: 'N/A', varianceLabel: 'Error' },
        yearlyTotals: { budget: 0, anticipated: 0, actual: 0, yearlyComparison: 0 }
      };
    }

    // Use actualsMap directly from prop, ensuring it includes the 'budget' field if available
    const actualsMap = customActualsData || {};
    const months = Array.isArray(customBudgetData?.months) ? customBudgetData.months : [];
    const totalYearlyBudget = customBudgetData.summary?.totalExpenses || 0; // Base yearly budget

    if (months.length === 0) {
      // Handle case where months array is empty
       return { error: "No months found in budget data.", months: [], monthlyComparisonData: [], categoryData: [], monthlyExpensesByCategory: [], pieChartData: [], actualSubcategoryTotals: {}, displayTotals: { budget: 0, comparison: 0, variance: 0, percentOfBudget: 'N/A', varianceLabel: 'Error' }, yearlyTotals: { budget: 0, anticipated: 0, actual: 0, yearlyComparison: 0 } };
    }

    try {
      // --- Prepare Data Structures ---
      const budgetExpenses = customBudgetData.expenses || {};
      const anticipatedMonthlyTotals = customExpenseData.monthlyTotals || [];
      // Define mapping from display name to key used in actuals/anticipated data
      const categoryDisplayToDataKey = { Compensation: 'compensation', Projects: 'consultancy', Subscriptions: 'subscriptions', Other: 'other' };


      // --- Calculate Monthly Data ---
      const monthlyData = months.map((month, monthIndex) => {
        const actualMonthDoc = actualsMap[month]; // Full actuals doc for the month
        const actualMonthData = actualMonthDoc?.data || {};
        const anticipatedMonth = anticipatedMonthlyTotals.find(item => item.month === month) || {};

        let monthlyBudgetTotal = 0;
        let monthlyComparisonValue = 0;
        let monthlyComparisonType = 'Anticipated'; // Default
        const monthlyBudgetByCategory = { Compensation: 0, Projects: 0, Subscriptions: 0, Other: 0 };
        const monthlyComparisonByCategory = { Compensation: 0, Projects: 0, Subscriptions: 0, Other: 0 };


        // **MODIFIED: Determine Monthly Budget Total**
        // Prioritize budget stored with actuals
        if (actualMonthDoc?.budget !== undefined && actualMonthDoc?.budget !== null && isValidNumber(actualMonthDoc.budget)) {
             monthlyBudgetTotal = actualMonthDoc.budget;
             console.log(`[Frontend Calc ${month}] Using stored budget: ${monthlyBudgetTotal}`);
        } else {
             // Fallback: Calculate monthly budget by summing subcategories from budgetData
             console.log(`[Frontend Calc ${month}] Stored budget not found/invalid (${actualMonthDoc?.budget}). Calculating...`);
             Object.keys(budgetExpenses).forEach(categoryKey => {
                 // Ensure the category exists before trying to access its properties
                 if (budgetExpenses[categoryKey]?.categories) {
                     Object.values(budgetExpenses[categoryKey].categories).forEach(subCat => {
                         const budgetValue = Array.isArray(subCat.monthly)
                           ? (subCat.monthly[monthIndex] ?? 0)
                           : (subCat.monthly ?? 0);
                         monthlyBudgetTotal += budgetValue;
                     });
                 }
             });
              console.log(`[Frontend Calc ${month}] Calculated budget: ${monthlyBudgetTotal}`);
        }

        // Determine Monthly Comparison Value (Actuals > Anticipated)
        if (actualMonthData?.grandTotal !== undefined && actualMonthData?.grandTotal !== null && isValidNumber(actualMonthData.grandTotal)) {
          monthlyComparisonValue = actualMonthData.grandTotal;
          monthlyComparisonType = 'Actual';
        } else {
          monthlyComparisonValue = anticipatedMonth.total || 0;
          monthlyComparisonType = 'Anticipated';
        }

        // Calculate Monthly Variance
        const monthlyVariance = monthlyComparisonValue - monthlyBudgetTotal;

        // **MODIFIED: Calculate Monthly Budget & Comparison By Category using Proration**
        // Use the determined monthlyBudgetTotal and prorate based on yearly budget category totals
        Object.entries(categoryDisplayToDataKey).forEach(([displayCategory, dataKey]) => {
             const yearlyCategoryBudget = customBudgetData.expenses?.[dataKey]?.total || 0;
             // Ensure totalYearlyBudget is not zero to avoid division by zero
             const proportion = totalYearlyBudget > 0 ? (yearlyCategoryBudget / totalYearlyBudget) : 0;
             const estimatedMonthlyCatBudget = monthlyBudgetTotal * proportion;
             monthlyBudgetByCategory[displayCategory] = estimatedMonthlyCatBudget;

             // Get comparison value for the category (Actual if available, else Anticipated)
             let comparisonCatValue = anticipatedMonth[dataKey] || 0; // Default to anticipated
             if (monthlyComparisonType === 'Actual' && actualMonthData[dataKey]?.total !== undefined && actualMonthData[dataKey]?.total !== null) {
                 comparisonCatValue = actualMonthData[dataKey].total;
             }
             monthlyComparisonByCategory[displayCategory] = comparisonCatValue;
        });


        return {
          month,
          budget: monthlyBudgetTotal, // Use the determined total budget
          comparison: monthlyComparisonValue,
          comparisonType: monthlyComparisonType,
          variance: monthlyVariance,
          budgetByCategory: monthlyBudgetByCategory, // Prorated budget
          comparisonByCategory: monthlyComparisonByCategory, // Actual/Anticipated
          actualSubcategories: actualMonthData // Keep for pie chart drilldown
        };
      });

      // --- Calculate Yearly Aggregations ---
      // Note: Yearly totals displayed in the header now come from this calculation
      let yearlyBudgetTotal = 0; // Sum of monthly budgets used
      let yearlyComparisonTotal = 0; // Sum of monthly comparison values used
      let yearlyActualTotal = 0; // Sum of actuals where available
      let yearlyAnticipatedTotal = customExpenseData.summary?.totalExpenses || 0; // Overall anticipated
      const yearlyBudgetByCategory = { Compensation: 0, Projects: 0, Subscriptions: 0, Other: 0 };
      const yearlyComparisonByCategory = { Compensation: 0, Projects: 0, Subscriptions: 0, Other: 0 };
      const yearlyActualSubcategoryTotals = { Compensation: {}, Projects: {}, Subscriptions: {}, Other: {} }; // For pie drilldown

      monthlyData.forEach(data => {
        yearlyBudgetTotal += data.budget; // Sum up the monthly budgets (stored or calculated)
        yearlyComparisonTotal += data.comparison; // Sum up the monthly comparison values (actual or anticipated)
        yearlyActualTotal += (data.comparisonType === 'Actual' ? data.comparison : 0); // Sum only actuals

        Object.keys(yearlyBudgetByCategory).forEach(cat => {
          yearlyBudgetByCategory[cat] += data.budgetByCategory[cat] || 0; // Sum prorated monthly budgets
          yearlyComparisonByCategory[cat] += data.comparisonByCategory[cat] || 0; // Sum monthly comparison values
        });

        // Aggregate actual subcategories for pie drilldown (remains same)
        Object.entries(categoryDisplayToDataKey).forEach(([displayCategory, actualsKey]) => {
            const actualSubData = data.actualSubcategories?.[actualsKey]?.subcategories;
            if (actualSubData) {
                Object.entries(actualSubData).forEach(([subName, subValue]) => {
                    if (!yearlyActualSubcategoryTotals[displayCategory][subName]) {
                        yearlyActualSubcategoryTotals[displayCategory][subName] = 0;
                    }
                    yearlyActualSubcategoryTotals[displayCategory][subName] += (isValidNumber(subValue) ? subValue : 0);
                });
            }
        });
      });

      const formattedYearlyActualSubcategoryTotals = {};
      Object.keys(yearlyActualSubcategoryTotals).forEach(categoryDisplayName => {
          formattedYearlyActualSubcategoryTotals[categoryDisplayName] = Object.entries(yearlyActualSubcategoryTotals[categoryDisplayName])
              .map(([name, value]) => ({ name, value }))
              .filter(item => item.value !== 0);
      });

      // --- Prepare Data for Charts based on View Mode ---
      let finalMonthlyComparisonData = [];
      let finalCategoryData = [];
      let finalMonthlyExpensesByCategory = [];
      let finalPieChartData = [];
      let finalActualSubcategoryTotals = {};

      if (viewMode === 'yearly') {
        finalMonthlyComparisonData = monthlyData; // Table uses this directly now
        finalCategoryData = Object.keys(yearlyBudgetByCategory).map(name => ({
          name,
          budget: yearlyBudgetByCategory[name], // Sum of prorated monthly budgets
          comparison: yearlyComparisonByCategory[name] // Sum of monthly comparison values
        })).filter(item => isValidNumber(item.budget) && isValidNumber(item.comparison) && (item.budget > 0 || item.comparison > 0));
        finalMonthlyExpensesByCategory = monthlyData.map(d => ({
            month: d.month,
            ...d.comparisonByCategory // Use monthly comparison category values
        }));
        finalPieChartData = Object.entries(yearlyComparisonByCategory) // Based on summed monthly comparison values
          .map(([name, value]) => ({ name, value }))
          .filter(item => isValidNumber(item.value) && item.value > 0);
        finalActualSubcategoryTotals = formattedYearlyActualSubcategoryTotals; // From actuals

      } else { // Monthly view
        const monthDetail = monthlyData.find(d => d.month === selectedMonth);
        if (monthDetail) {
          finalMonthlyComparisonData = [monthDetail]; // Table shows only selected month
          finalCategoryData = Object.keys(monthDetail.budgetByCategory).map(name => ({
            name,
            budget: monthDetail.budgetByCategory[name], // Prorated monthly budget
            comparison: monthDetail.comparisonByCategory[name] // Actual/Anticipated category value
          })).filter(item => isValidNumber(item.budget) && isValidNumber(item.comparison) && (item.budget > 0 || item.comparison > 0));
           finalMonthlyExpensesByCategory = [{ // Stacked bar shows only selected month
               month: monthDetail.month,
               ...monthDetail.comparisonByCategory
           }];
          finalPieChartData = Object.entries(monthDetail.comparisonByCategory) // Based on monthly comparison values
            .map(([name, value]) => ({ name, value }))
            .filter(item => isValidNumber(item.value) && item.value > 0);

          // Format monthly actual subcategories for pie drilldown
          const formattedMonthlyActualSubcategoryTotals = {};
           Object.entries(categoryDisplayToDataKey).forEach(([displayCategory, actualsKey]) => {
               const actualSubData = monthDetail.actualSubcategories?.[actualsKey]?.subcategories;
               if (actualSubData) {
                   formattedMonthlyActualSubcategoryTotals[displayCategory] = Object.entries(actualSubData)
                       .map(([name, value]) => ({ name, value }))
                       .filter(item => item.value !== 0);
               } else {
                   formattedMonthlyActualSubcategoryTotals[displayCategory] = [];
               }
           });
          finalActualSubcategoryTotals = formattedMonthlyActualSubcategoryTotals;

        } else {
           // Handle case where selected month data isn't found
           console.warn(`Data for selected month ${selectedMonth} not found.`);
           finalMonthlyComparisonData = []; finalCategoryData = []; finalMonthlyExpensesByCategory = []; finalPieChartData = []; finalActualSubcategoryTotals = {};
        }
      }

      // --- Calculate Overall Totals for Display ---
      // Use the totals calculated by summing the monthlyData figures
      const displayTotalBudget = yearlyBudgetTotal; // Sum of monthly budgets used
      const displayTotalComparison = yearlyComparisonTotal; // Sum of monthly comparison values used
      const displayTotalVariance = displayTotalComparison - displayTotalBudget;
      const displayPercentOfBudget = displayTotalBudget ? ((displayTotalComparison / displayTotalBudget) * 100).toFixed(1) : 'N/A';
      // Determine variance label based on whether *any* actual data contributed to the comparison total
      const displayVarianceLabel = (yearlyActualTotal > 0)
          ? "Actual/Ant. vs Budget"
          : "Anticipated vs Budget";

      // Return the final processed data structure
      return {
        error: null,
        months: months,
        monthlyComparisonData: finalMonthlyComparisonData, // Used by table and potentially bar chart
        categoryData: finalCategoryData, // Used by category comparison bar chart
        monthlyExpensesByCategory: finalMonthlyExpensesByCategory, // Used by stacked bar chart
        pieChartData: finalPieChartData, // Used by pie chart (top level)
        actualSubcategoryTotals: finalActualSubcategoryTotals, // Used by pie chart (drilldown)
        displayTotals: { // Used by header summary line
            budget: displayTotalBudget,
            comparison: displayTotalComparison,
            variance: displayTotalVariance,
            percentOfBudget: displayPercentOfBudget,
            varianceLabel: displayVarianceLabel,
        },
         yearlyTotals: { // Keep original yearly totals if needed elsewhere
             budget: totalYearlyBudget, // Original yearly budget total
             anticipated: yearlyAnticipatedTotal,
             actual: yearlyActualTotal, // Sum of actuals only
             yearlyComparison: yearlyComparisonTotal // Sum of monthly actual/anticipated
         }
      };

    } catch (err) {
      console.error("Error processing visualization data:", err);
      return {
        error: `Failed to process data: ${err.message}`,
        months: [], monthlyComparisonData: [], categoryData: [],
        monthlyExpensesByCategory: [], pieChartData: [],
        actualSubcategoryTotals: {},
        displayTotals: { budget: 0, comparison: 0, variance: 0, percentOfBudget: 'N/A', varianceLabel: 'Error' },
        yearlyTotals: { budget: 0, anticipated: 0, actual: 0, yearlyComparison: 0 }
      };
    }
  }, [customBudgetData, customExpenseData, customActualsData, viewMode, selectedMonth, isValidNumber]); // Dependencies


  // Effect to update the pie chart data when the view mode or processed data changes
  useEffect(() => {
    if (processedData && !processedData.error) {
      setDrilldownCategory(null); // Reset drilldown when view changes
      setCurrentPieData(processedData.pieChartData || []);
    } else {
      setCurrentPieData([]);
      setDrilldownCategory(null);
    }
  }, [processedData, viewMode]); // Depend on processedData and viewMode

  // --- Event Handlers ---
  const handleViewModeChange = (event) => {
    setViewMode(event.target.value);
    // No need to reset drilldown here, useEffect above handles it
  };

  const handleChartTypeChange = (event) => {
    setChartType(event.target.value);
  };

  const handleMonthChange = (event) => {
    setSelectedMonth(event.target.value);
     // No need to reset drilldown here, useEffect above handles it
  };

   const handlePieClick = useCallback((data) => {
       // ... (pie click logic remains the same) ...
       console.log("Pie slice clicked:", data);
       const categoryName = data?.name;

       if (categoryName && !drilldownCategory) {
           console.log(`Attempting drilldown for category: ${categoryName}`);
           const subData = processedData?.actualSubcategoryTotals?.[categoryName];
           console.log(`Subcategory data found for ${categoryName} (in current view):`, subData);

           if (subData && Array.isArray(subData) && subData.length > 0) {
               console.log(`Drilling down into subcategories for ${categoryName}`);
               setDrilldownCategory(categoryName);
               setCurrentPieData(subData);
           } else {
               console.log(`No actual subcategory data available (or length is 0) to drill down for ${categoryName} in the current view.`);
           }
       } else if (drilldownCategory) {
           console.log("Already drilled down or attempting drilldown on subcategory, click ignored for further drilldown.");
       } else {
           console.log("Click ignored (no data name).");
       }
   }, [drilldownCategory, processedData]);


  const handleBackToCategories = useCallback(() => {
    setDrilldownCategory(null);
    // Reset pie data based on the main processed data
    setCurrentPieData(processedData?.pieChartData || []);
  }, [processedData]);

  // --- Custom Tooltip Components ---
  // ... (CustomTooltip and PieCustomTooltip remain the same) ...
    // Tooltip for Bar Charts
  const CustomTooltip = useCallback(({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const dataPoint = payload[0].payload;
      const comparisonName = dataPoint.comparisonType || 'Actual/Ant.';
      const title = label || dataPoint.name || dataPoint.month || 'Details';
      // Calculate percentage of budget for comparison bar/stack total
      const comparisonTotalValue = payload.reduce((sum, entry) => (entry.name === 'comparison' || entry.stackId === 'a') ? sum + entry.value : sum, 0);
      const budgetValue = dataPoint.budget; // Budget for this specific item (month or category)
      const percentOfBudgetString = budgetValue && budgetValue !== 0 && isValidNumber(budgetValue) && isValidNumber(comparisonTotalValue)
          ? `(${(comparisonTotalValue / budgetValue * 100).toFixed(1)}% of Budget)`
          : '';

      return (
        <div className="bg-white p-3 border border-gray-200 rounded shadow text-sm">
          <p className="font-bold mb-1">{title}</p>
          {payload.map((entry, index) => {
              // Determine the label for the entry
              let entryLabel = entry.name;
              if (entry.name === 'comparison') {
                  entryLabel = comparisonName;
              } else if (entry.stackId === 'a' && chartType === 'stacked' && viewMode === 'monthly') {
                  // For stacked monthly view, the payload name is the category
                  entryLabel = entry.name;
              } else if (entry.stackId === 'a' && chartType === 'stacked' && viewMode === 'yearly') {
                   // For stacked yearly view, the payload name is the category
                   entryLabel = entry.name;
              }

              // Add percentage for the comparison bar/stack total
              const valueString = formatCurrency(entry.value);
              const displayString = (entry.name === 'comparison' || entry.stackId === 'a')
                  ? `${entryLabel}: ${valueString} ${percentOfBudgetString}`
                  : `${entryLabel}: ${valueString}`;

              return (
                <p key={`tooltip-${index}`} style={{ color: entry.color }}>
                  {displayString}
                </p>
              );
          })}
          {/* Show variance only if it exists and we are not in stacked view (where variance isn't directly applicable per stack segment) */}
          {dataPoint.variance !== undefined && isValidNumber(dataPoint.variance) && chartType !== 'stacked' && (
            <p className="mt-1 pt-1 border-t">
              Variance: <span className={dataPoint.variance >= 0 ? 'text-red-600' : 'text-green-600'}>
                {dataPoint.variance >= 0 ? '+' : ''}{formatCurrency(dataPoint.variance)}
              </span>
            </p>
          )}
        </div>
      );
    }
    return null;
  }, [formatCurrency, chartType, viewMode, isValidNumber]); // Add chartType and viewMode dependencies

  // Tooltip for Pie Chart
  const PieCustomTooltip = useCallback(({ active, payload }) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload;
      const currentTotal = currentPieData.reduce((sum, entry) => sum + (entry.value || 0), 0);
      const percentage = currentTotal && isValidNumber(currentTotal) && isValidNumber(data.value) ? ((data.value / currentTotal) * 100).toFixed(1) : 0;
      const tooltipTitle = drilldownCategory ? `${data.name} (Subcategory)` : data.name;

      return (
        <div className="bg-white p-2 border border-gray-200 rounded shadow text-sm">
          <p className="font-bold">{`${tooltipTitle}: ${formatCurrency(data.value)} (${percentage}%)`}</p>
        </div>
      );
    }
    return null;
  }, [formatCurrency, currentPieData, drilldownCategory, isValidNumber]);


  // --- Render Logic ---
  if (processedData.error) {
    return <div className="text-center p-4 text-red-600 bg-red-100 border border-red-400 rounded">{processedData.error}</div>;
  }
  if (!processedData || !processedData.months || processedData.months.length === 0) {
    return <div className="text-center p-4 text-gray-500">Loading data or no data available...</div>;
  }

  // Refined note for clarity
  const comparisonNote = "Comparison data uses Actuals where available for a given month/category, otherwise it uses Anticipated values.";
  const pieChartNote = drilldownCategory
    ? `Distribution based on Actual values for ${drilldownCategory} (${viewMode === 'monthly' ? selectedMonth : 'Yearly Total'}).`
    : `Distribution based on Actuals/Anticipated (${viewMode === 'monthly' ? selectedMonth : 'Yearly Total'}). Click slice to view actual subcategories if available.`;

   // Dynamic Titles (remain the same)
   const monthlyCompTitle = viewMode === 'monthly' ? `Budget vs Actual/Anticipated (${selectedMonth})` : 'Monthly Budget vs Actual/Anticipated';
   const categoryCompTitle = viewMode === 'monthly' ? `Category Budget vs Actual/Anticipated (${selectedMonth})` : 'Category Budget vs Actual/Anticipated (Overall)';
   const varianceTitle = viewMode === 'monthly' ? `Monthly Variance (${selectedMonth})` : 'Monthly Variance (Budget vs Actual/Anticipated)';
   const pieTitle = drilldownCategory ? `${drilldownCategory} Subcategories (${viewMode === 'monthly' ? selectedMonth : 'Actuals - Yearly'})` : `Expense Distribution (${viewMode === 'monthly' ? selectedMonth : 'Actual/Ant. - Yearly'})`;
   const tableTitle = viewMode === 'yearly' ? 'Yearly Summary by Month' : `${selectedMonth} Summary`;


  return (
    <div className="p-6 bg-gray-50">
      {/* Header Section */}
      <div className="mb-6 text-center border-b pb-4">
        <h1 className="text-3xl font-bold mb-2">FY26 Budget Analysis</h1>

         {/* Display Totals from processedData */}
         <p className="text-gray-600">
             Total Budget ({viewMode}): {formatCurrency(processedData.displayTotals.budget)} |
             Total Actual/Anticipated ({viewMode}): {formatCurrency(processedData.displayTotals.comparison)} |
             Variance ({processedData.displayTotals.varianceLabel}):
             <span className={processedData.displayTotals.variance >= 0 ? 'text-red-600' : 'text-green-600'}>
                 {processedData.displayTotals.variance >= 0 ? ' +' : ' '}{formatCurrency(processedData.displayTotals.variance)}
             </span> ({processedData.displayTotals.percentOfBudget}% of budget)
         </p>

         {/* AI Summary Display Area */}
         <div className="mt-3 text-sm text-center min-h-[20px]">
            {isSummaryLoading && (
                <p className="text-gray-500 italic">Loading AI summary...</p>
            )}
            {summaryError && (
                <p className="text-red-500 italic">Error loading summary: {summaryError}</p>
            )}
            {!isSummaryLoading && !summaryError && aiSummary && (
                <p className="text-blue-700 bg-blue-50 border border-blue-200 rounded px-3 py-1 inline-block italic">
                    <strong>AI Overview:</strong> {aiSummary}
                </p>
            )}
             {!isSummaryLoading && !summaryError && !aiSummary && (
                 <p className="text-gray-400 italic">AI summary not available.</p>
             )}
         </div>

        {/* Controls */}
        <div className="mt-4 flex gap-4 justify-center">
          {/* ... Controls remain the same ... */}
          <div>
            <label htmlFor="viewModeSelect" className="mr-2 font-medium">View:</label>
            <select
              id="viewModeSelect"
              value={viewMode}
              onChange={handleViewModeChange}
              className="border rounded p-2 bg-white cursor-pointer focus:ring-blue-500 focus:border-blue-500"
            >
              <option value="yearly">Yearly</option>
              <option value="monthly">Monthly</option>
            </select>
          </div>
          {viewMode === 'monthly' && (
            <div>
              <label htmlFor="monthSelect" className="mr-2 font-medium">Month:</label>
              <select
                id="monthSelect"
                value={selectedMonth}
                onChange={handleMonthChange}
                className="border rounded p-2 bg-white cursor-pointer focus:ring-blue-500 focus:border-blue-500"
              >
                {availableMonths.map(month => (
                  <option key={month} value={month}>{month}</option>
                ))}
              </select>
            </div>
          )}
          <div>
            <label htmlFor="chartTypeSelect" className="mr-2 font-medium">Chart Type (1st Chart):</label>
            <select
              id="chartTypeSelect"
              value={chartType}
              onChange={handleChartTypeChange}
              className="border rounded p-2 bg-white cursor-pointer focus:ring-blue-500 focus:border-blue-500"
            >
              <option value="bar">Budget vs Actual/Ant.</option>
              <option value="stacked">Category Breakdown</option>
            </select>
          </div>
          {/* Removed AI Insight Panel Toggle Button from here */}
        </div>
      </div>

      {/* Charts Section */}
      {/* ... Charts rendering logic remains the same, using processedData ... */}
       <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">

        {/* Chart 1 */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-bold mb-4">{chartType === 'bar' ? monthlyCompTitle : `Category Breakdown (${viewMode === 'monthly' ? selectedMonth : 'Monthly'})`}</h2>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              {chartType === 'bar' ? (
                <BarChart data={processedData.monthlyComparisonData} margin={{ top: 10, right: 30, left: 65, bottom: 60 }}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="month" angle={viewMode === 'yearly' ? -45 : 0} textAnchor={viewMode === 'yearly' ? "end" : "middle"} height={viewMode === 'yearly' ? 70 : 30} tick={{ fontSize: 14 }} interval={0} />
                  <YAxis tickFormatter={(value) => `R${(value / 1000).toLocaleString()}k`} width={65} tick={{ fontSize: 14 }} />
                  <Tooltip content={<CustomTooltip />} />
                  <Legend wrapperStyle={{ paddingTop: 10 }} />
                  <Bar dataKey="budget" name="Budget" fill="#0088FE" />
                  <Bar dataKey="comparison" name="Actual/Anticipated" fill="#FF8042" />
                </BarChart>
              ) : (
                <BarChart data={processedData.monthlyExpensesByCategory} margin={{ top: 10, right: 30, left: 65, bottom: 60 }}>
                  <CartesianGrid strokeDasharray="3 3" />
                   <XAxis dataKey="month" angle={viewMode === 'yearly' ? -45 : 0} textAnchor={viewMode === 'yearly' ? "end" : "middle"} height={viewMode === 'yearly' ? 70 : 30} tick={{ fontSize: 14 }} interval={0} />
                  <YAxis tickFormatter={(value) => `R${(value / 1000).toLocaleString()}k`} width={65} tick={{ fontSize: 14 }} />
                  <Tooltip content={<CustomTooltip />} />
                  <Legend wrapperStyle={{ paddingTop: 10 }} />
                  <Bar dataKey="Compensation" stackId="a" name="Compensation" fill="#0088FE" />
                  <Bar dataKey="Projects" stackId="a" name="Projects" fill="#00C49F" />
                  <Bar dataKey="Subscriptions" stackId="a" name="Subscriptions" fill="#FFBB28" />
                  <Bar dataKey="Other" stackId="a" name="Other" fill="#FF8042" />
                </BarChart>
              )}
            </ResponsiveContainer>
          </div>
           <p className="text-sm text-gray-500 mt-2 text-center">{comparisonNote}</p>
        </div>

        {/* Chart 2 */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-bold mb-4">{categoryCompTitle}</h2>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <BarChart data={processedData.categoryData} margin={{ top: 10, right: 30, left: 65, bottom: 60 }} barSize={40}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" angle={-45} textAnchor="end" height={70} tick={{ fontSize: 11 }} interval={0} />
                <YAxis tickFormatter={(value) => `R${(value / 1000).toLocaleString()}k`} width={65} tick={{ fontSize: 14 }} />
                <Tooltip content={<CustomTooltip />} />
                <Legend wrapperStyle={{ paddingTop: 10 }} />
                <Bar dataKey="budget" name="Budget" fill="#0088FE" />
                <Bar dataKey="comparison" name="Actual/Anticipated" fill="#FF8042" />
              </BarChart>
            </ResponsiveContainer>
          </div>
           <p className="text-sm text-gray-500 mt-2 text-center">{comparisonNote}</p>
        </div>

        {/* Chart 3 */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-bold mb-4">{varianceTitle}</h2>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <BarChart data={processedData.monthlyComparisonData} margin={{ top: 10, right: 30, left: 65, bottom: 60 }}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="month" angle={viewMode === 'yearly' ? -45 : 0} textAnchor={viewMode === 'yearly' ? "end" : "middle"} height={viewMode === 'yearly' ? 70 : 30} tick={{ fontSize: 14 }} interval={0} />
                <YAxis tickFormatter={(value) => `R${(value / 1000).toLocaleString()}k`} width={65} tick={{ fontSize: 14 }} />
                <Tooltip content={<CustomTooltip />} />
                <Bar dataKey="variance" name="Variance">
                  {processedData.monthlyComparisonData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.variance >= 0 ? '#ef4444' : '#22c55e'} />
                  ))}
                </Bar>
              </BarChart>
            </ResponsiveContainer>
          </div>
          <p className="text-sm text-center mt-2">
            <span className="inline-block w-3 h-3 bg-red-500 mr-1 align-middle"></span> Over Budget
            <span className="inline-block ml-4 w-3 h-3 bg-green-500 mr-1 align-middle"></span> Under Budget
          </p>
           <p className="text-sm text-gray-500 mt-2 text-center">Variance calculated against {comparisonNote.toLowerCase()}</p>
        </div>

        {/* Chart 4 */}
        <div className="bg-white p-6 rounded-lg shadow-md">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-bold">{pieTitle}</h2>
            {drilldownCategory && (
              <button
                onClick={handleBackToCategories}
                className="text-sm text-blue-600 hover:text-blue-800"
              >
                &larr; Back to Categories
              </button>
            )}
          </div>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <PieChart>
                <Pie
                  data={currentPieData}
                  cx="50%"
                  cy="50%"
                  labelLine={false}
                  outerRadius={100}
                  fill="#8884d8"
                  dataKey="value"
                  nameKey="name"
                  onClick={handlePieClick}
                  style={{ cursor: drilldownCategory ? 'default' : 'pointer' }}
                >
                  {currentPieData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip content={<PieCustomTooltip />} />
                <Legend />
              </PieChart>
            </ResponsiveContainer>
          </div>
          <p className="text-sm text-gray-500 mt-2 text-center">
            {pieChartNote}
          </p>
        </div>
      </div>


      {/* Summary Table */}
      <div className="mt-8 bg-white p-6 rounded-lg shadow-md overflow-auto">
        <h2 className="text-xl font-bold mb-4">{tableTitle}</h2>
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Month</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Budget</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actual/Ant.</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Type</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Variance</th>
              <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">% of Budget</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {/* Use monthlyComparisonData which now has corrected budget */}
            {processedData.monthlyComparisonData.map((item, index) => {
              const percentOfBud = isValidNumber(item.budget) && item.budget !== 0 && isValidNumber(item.comparison)
                  ? ((item.comparison / item.budget) * 100).toFixed(1) : 'N/A';
              return (
                <tr key={item.month} className={index % 2 === 0 ? 'bg-white' : 'bg-gray-50'}>
                  <td className="px-4 py-2 whitespace-nowrap">{item.month}</td>
                  <td className="px-4 py-2 whitespace-nowrap">{formatCurrency(item.budget)}</td> {/* Should now show correct budget */}
                  <td className="px-4 py-2 whitespace-nowrap">{formatCurrency(item.comparison)}</td>
                  <td className="px-4 py-2 whitespace-nowrap">{item.comparisonType}</td>
                  <td className={`px-4 py-2 whitespace-nowrap ${item.variance >= 0 ? 'text-red-600' : 'text-green-600'}`}>
                    {item.variance === 0 ? '0' : (item.variance > 0 ? '+' : '')}{formatCurrency(item.variance)}
                  </td>
                  <td className="px-4 py-2 whitespace-nowrap">{percentOfBud}%</td>
                </tr>
              );
            })}
            {/* Display overall totals calculated by summing monthly data */}
            {viewMode === 'yearly' && (
              <tr className="bg-gray-100 font-bold">
                <td className="px-4 py-2 whitespace-nowrap">TOTAL</td>
                <td className="px-4 py-2 whitespace-nowrap">{formatCurrency(processedData.displayTotals.budget)}</td>
                <td className="px-4 py-2 whitespace-nowrap">{formatCurrency(processedData.displayTotals.comparison)}</td>
                 {/* Determine overall type based on whether any actuals contributed */}
                <td className="px-4 py-2 whitespace-nowrap">{processedData.yearlyTotals.actual > 0 ? 'Actual*' : 'Anticipated'}</td>
                <td className={`px-4 py-2 whitespace-nowrap ${processedData.displayTotals.variance >= 0 ? 'text-red-600' : 'text-green-600'}`}>
                  {processedData.displayTotals.variance === 0 ? '0' : (processedData.displayTotals.variance > 0 ? '+' : '')}{formatCurrency(processedData.displayTotals.variance)}
                </td>
                <td className="px-4 py-2 whitespace-nowrap">{processedData.displayTotals.percentOfBudget}%</td>
              </tr>
            )}
          </tbody>
        </table>
         {viewMode === 'yearly' && processedData.yearlyTotals.actual > 0 && (
               <p className="text-xs text-gray-500 mt-1 text-right">* Total Actual/Ant. uses Actuals for months where available, Anticipated otherwise.</p>
         )}
      </div>

      {/* AI Insight Panel Component (remains) */}
      <AISidePanel
        isOpen={isAIPanelOpen}
        currentContext={currentAIContext}
        onClose={() => setIsAIPanelOpen(false)}
      />

      {/* Floating Action Button for AI Insights */}
      <Box
        sx={{
          position: 'fixed',
          bottom: 32, // Adjust spacing as needed
          right: 32, // Adjust spacing as needed
          zIndex: 1050, // Ensure it's above other elements if necessary
        }}
      >
        <Fab
          color="primary"
          aria-label="ai insights"
          onClick={() => {
            setIsAIPanelOpen(true);
            setIsRotating(true);
            setTimeout(() => setIsRotating(false), 500); // Rotate for 500ms
          }}
          title="Open AI Insights Panel"
        >
          <AutoAwesomeIcon
            sx={{
              transition: 'transform 0.5s ease-in-out',
              transform: isRotating ? 'rotate(360deg)' : 'rotate(0deg)',
            }}
          />
        </Fab>
      </Box>
    </div>
  );
};

export default BudgetVisualization;
