import React, { useState, useEffect } from 'react';

// This file should be saved as src/ExpenseEditor.jsx
const ExpenseEditor = ({ initialData, onDataUpdate }) => {
  // States for expense data and UI
  const [expenseData, setExpenseData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [currentCategory, setCurrentCategory] = useState('compensation');
  const [currentSubcategory, setCurrentSubcategory] = useState(null);
  const [editMode, setEditMode] = useState(false);
  const [months, setMonths] = useState([]);
  
  // Form state for adding/editing expenses
  const [formData, setFormData] = useState({
    category: 'compensation',
    subcategory: '',
    isNewSubcategory: false,
    isMonthly: true,
    monthlyValue: 0,
    specificMonths: {}
  });

  // Use initialData passed from parent component
  useEffect(() => {
    if (initialData) {
      setExpenseData(initialData);
      setMonths(initialData.months);
      setLoading(false);
    } else {
      // Fallback to loading data directly if initialData is not provided
      const loadData = async () => {
        try {
          setLoading(true);
          // Try to load using window.fs
          if (typeof window !== 'undefined' && window.fs && typeof window.fs.readFile === 'function') {
            const expensesResponse = await window.fs.readFile('anticipated-expenses.json', { encoding: 'utf8' });
            const parsedData = JSON.parse(expensesResponse);
            setExpenseData(parsedData);
            setMonths(parsedData.months);
          } else {
            // Fallback to fetch
            const response = await fetch('/anticipated-expenses.json');
            if (!response.ok) {
              throw new Error(`Error loading expense data: ${response.statusText}`);
            }
            const parsedData = await response.json();
            setExpenseData(parsedData);
            setMonths(parsedData.months);
          }
        } catch (err) {
          setError(`Failed to load expense data: ${err.message}`);
          console.error('Error loading expense data:', err);
        } finally {
          setLoading(false);
        }
      };

      loadData();
    }
  }, [initialData]);

  // Reset form when changing categories or subcategories
  useEffect(() => {
    if (currentSubcategory && expenseData) {
      const categoryData = expenseData.expenses[currentCategory].categories[currentSubcategory];
      
      // Check if monthly is an array or a single number
      const isMonthlyArray = Array.isArray(categoryData.monthly);
      
      setFormData({
        category: currentCategory,
        subcategory: currentSubcategory,
        isNewSubcategory: false,
        isMonthly: !isMonthlyArray,
        monthlyValue: isMonthlyArray ? 0 : categoryData.monthly,
        specificMonths: isMonthlyArray 
          ? months.reduce((acc, month, index) => {
              acc[month] = categoryData.monthly[index] || 0;
              return acc;
            }, {})
          : months.reduce((acc, month) => {
              acc[month] = categoryData.monthly;
              return acc;
            }, {})
      });
    } else {
      // Reset form for new entry
      setFormData({
        category: currentCategory,
        subcategory: '',
        isNewSubcategory: true,
        isMonthly: true,
        monthlyValue: 0,
        specificMonths: months.reduce((acc, month) => {
          acc[month] = 0;
          return acc;
        }, {})
      });
    }
  }, [currentCategory, currentSubcategory, expenseData, months]);

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    if (type === 'checkbox') {
      setFormData({ ...formData, [name]: checked });
    } else if (name === 'monthlyValue') {
      setFormData({ ...formData, [name]: parseFloat(value) || 0 });
    } else if (name.startsWith('month_')) {
      const month = name.replace('month_', '');
      setFormData({
        ...formData,
        specificMonths: {
          ...formData.specificMonths,
          [month]: parseFloat(value) || 0
        }
      });
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  // Calculate the total expense based on current form data
  const calculateTotal = () => {
    if (formData.isMonthly) {
      return formData.monthlyValue * months.length;
    } else {
      return Object.values(formData.specificMonths).reduce((sum, value) => sum + value, 0);
    }
  };

  // Open the modal to add/edit an expense
  const openModal = (category, subcategory = null) => {
    setCurrentCategory(category);
    setCurrentSubcategory(subcategory);
    setEditMode(!!subcategory);
    setModalOpen(true);
  };

  // Close the modal
  const closeModal = () => {
    setModalOpen(false);
    setCurrentSubcategory(null);
  };

  // Save the form data to the expense object
  const saveExpense = () => {
    if (!formData.subcategory) {
      alert('Please enter a subcategory name');
      return;
    }

    // Create a deep copy of the expense data to modify
    const updatedData = JSON.parse(JSON.stringify(expenseData));
    
    // Prepare the monthly value (single number or array)
    let monthlyValue;
    if (formData.isMonthly) {
      monthlyValue = formData.monthlyValue;
    } else {
      monthlyValue = months.map(month => formData.specificMonths[month] || 0);
    }

    // Update the category data
    updatedData.expenses[formData.category].categories[formData.subcategory] = {
      monthly: monthlyValue,
      total: calculateTotal()
    };

    // Recalculate category total
    updatedData.expenses[formData.category].total = Object.values(updatedData.expenses[formData.category].categories)
      .reduce((sum, category) => sum + category.total, 0);

    // Recalculate monthly totals
    updatedData.monthlyTotals = months.map((month, index) => {
      const monthData = {
        month: month,
        compensation: 0,
        consultancy: 0,
        subscriptions: 0,
        other: 0,
        total: 0
      };

      // Calculate the values for each category
      Object.entries(updatedData.expenses).forEach(([categoryKey, category]) => {
        Object.entries(category.categories).forEach(([_, subcategory]) => {
          const monthValue = Array.isArray(subcategory.monthly) 
            ? (subcategory.monthly[index] || 0)
            : subcategory.monthly;
          
          monthData[categoryKey] += monthValue;
          monthData.total += monthValue;
        });
      });

      return monthData;
    });

    // Recalculate the summary totals
    updatedData.summary = {
      totalExpenses: Object.values(updatedData.expenses)
        .reduce((sum, category) => sum + category.total, 0),
      categoryTotals: Object.entries(updatedData.expenses)
        .reduce((totals, [key, category]) => {
          totals[key] = category.total;
          return totals;
        }, {})
    };

    // Update the state
    setExpenseData(updatedData);
    // Notify the parent component about the data update
    if (onDataUpdate) {
      onDataUpdate(updatedData);
    }
    closeModal();
  };

  // Export the updated expense data to a new JSON file
  const exportToJson = () => {
    if (!expenseData) return;

    const timestamp = new Date().toISOString().replace(/[:.]/g, '-');
    const fileName = `anticipated-expenses-${timestamp}.json`;
    const jsonData = JSON.stringify(expenseData, null, 2);
    
    // Create a blob from the JSON data
    const blob = new Blob([jsonData], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    
    // Create a link and trigger download
    const link = document.createElement('a');
    link.href = url;
    link.download = fileName;
    document.body.appendChild(link);
    link.click();
    
    // Clean up
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  // Helper to format currency
  const formatCurrency = (value) => {
    return new Intl.NumberFormat('en-ZA', {
      style: 'currency',
      currency: 'ZAR',
      maximumFractionDigits: 0
    }).format(value);
  };

  // Display loading state
  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="text-xl">Loading expense data...</div>
      </div>
    );
  }

  // Display error state
  if (error) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="text-xl text-red-600">{error}</div>
      </div>
    );
  }

  // Main component render
  return (
    <div className="p-6 bg-gray-50">
      <div className="mb-8">
        <h1 className="text-3xl font-bold mb-2">Anticipated Expenses Editor</h1>
        <p className="text-gray-600 mb-4">Add, modify, or delete anticipated expenses.</p>
        
        <div className="flex justify-between mb-4">
          <div>
            <button 
              onClick={exportToJson}
              className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
            >
              Export Changes to New JSON
            </button>
          </div>
          
          <div>
            <span className="text-gray-700 mr-2">Current Total:</span>
            <span className="font-bold">{formatCurrency(expenseData?.summary?.totalExpenses || 0)}</span>
          </div>
        </div>
      </div>

      {/* Category Tabs */}
      <div className="mb-6 border-b border-gray-200">
        <ul className="flex flex-wrap -mb-px">
          {expenseData && Object.keys(expenseData.expenses).map(category => (
            <li key={category} className="mr-2">
              <button
                className={`inline-block py-2 px-4 text-sm font-medium ${
                  currentCategory === category
                    ? 'text-blue-600 border-b-2 border-blue-600'
                    : 'text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
                onClick={() => setCurrentCategory(category)}
              >
                {category.charAt(0).toUpperCase() + category.slice(1)}
              </button>
            </li>
          ))}
        </ul>
      </div>

      {/* Expense List */}
      <div className="bg-white rounded-lg shadow mb-6">
        <div className="flex justify-between items-center p-4 border-b">
          <h2 className="text-xl font-semibold">
            {currentCategory.charAt(0).toUpperCase() + currentCategory.slice(1)} Expenses
          </h2>
          <button
            onClick={() => openModal(currentCategory)}
            className="bg-green-600 text-white px-3 py-1 rounded hover:bg-green-700"
          >
            Add New
          </button>
        </div>
        
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Name</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Type</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Monthly</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Total</th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {expenseData && Object.entries(expenseData.expenses[currentCategory].categories).map(([name, data]) => (
                <tr key={name}>
                  <td className="px-4 py-2">{name}</td>
                  <td className="px-4 py-2">
                    {Array.isArray(data.monthly) ? 'Variable Monthly' : 'Fixed Monthly'}
                  </td>
                  <td className="px-4 py-2">
                    {Array.isArray(data.monthly) 
                      ? `${formatCurrency(Math.min(...data.monthly))} - ${formatCurrency(Math.max(...data.monthly))}`
                      : formatCurrency(data.monthly)
                    }
                  </td>
                  <td className="px-4 py-2">{formatCurrency(data.total)}</td>
                  <td className="px-4 py-2 space-x-2">
                    <button
                      onClick={() => openModal(currentCategory, name)}
                      className="text-blue-600 hover:text-blue-800"
                    >
                      Edit
                    </button>
                  </td>
                </tr>
              ))}
              {expenseData && Object.entries(expenseData.expenses[currentCategory].categories).length === 0 && (
                <tr>
                  <td colSpan="5" className="px-4 py-4 text-center text-gray-500">
                    No expenses found. Click "Add New" to create one.
                  </td>
                </tr>
              )}
            </tbody>
            <tfoot className="bg-gray-50">
              <tr>
                <td className="px-4 py-3 font-bold">Total</td>
                <td className="px-4 py-3"></td>
                <td className="px-4 py-3"></td>
                <td className="px-4 py-3 font-bold">{formatCurrency(expenseData?.expenses[currentCategory].total || 0)}</td>
                <td className="px-4 py-3"></td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>

      {/* Modal for adding/editing expenses */}
      {modalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-lg w-full max-w-2xl overflow-hidden">
            <div className="p-4 border-b">
              <h2 className="text-xl font-semibold">
                {editMode ? `Edit ${currentSubcategory}` : 'Add New Expense'}
              </h2>
            </div>
            
            <div className="p-4">
              <div className="mb-4">
                <label className="block text-gray-700 mb-2">Category</label>
                <select
                  name="category"
                  value={formData.category}
                  onChange={handleInputChange}
                  className="w-full border rounded p-2"
                  disabled={editMode}
                >
                  {expenseData && Object.keys(expenseData.expenses).map(category => (
                    <option key={category} value={category}>
                      {category.charAt(0).toUpperCase() + category.slice(1)}
                    </option>
                  ))}
                </select>
              </div>
              
              <div className="mb-4">
                <label className="block text-gray-700 mb-2">Subcategory Name</label>
                <input
                  type="text"
                  name="subcategory"
                  value={formData.subcategory}
                  onChange={handleInputChange}
                  className="w-full border rounded p-2"
                  disabled={editMode}
                  placeholder="e.g., Consulting Service, Software Subscription"
                />
              </div>
              
              <div className="mb-4">
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    name="isMonthly"
                    checked={formData.isMonthly}
                    onChange={handleInputChange}
                    className="mr-2"
                  />
                  <span>Fixed monthly amount (same value for all months)</span>
                </label>
              </div>
              
              {formData.isMonthly ? (
                <div className="mb-4">
                  <label className="block text-gray-700 mb-2">Monthly Amount</label>
                  <input
                    type="number"
                    name="monthlyValue"
                    value={formData.monthlyValue}
                    onChange={handleInputChange}
                    className="w-full border rounded p-2"
                    min="0"
                    step="1000"
                  />
                </div>
              ) : (
                <div className="mb-4">
                  <label className="block text-gray-700 mb-2">Monthly Amounts</label>
                  <div className="grid grid-cols-3 gap-3">
                    {months.map(month => (
                      <div key={month} className="mb-2">
                        <label className="block text-gray-600 text-sm mb-1">{month}</label>
                        <input
                          type="number"
                          name={`month_${month}`}
                          value={formData.specificMonths[month] || 0}
                          onChange={handleInputChange}
                          className="w-full border rounded p-2"
                          min="0"
                          step="1000"
                        />
                      </div>
                    ))}
                  </div>
                </div>
              )}
              
              <div className="mb-4 p-4 bg-gray-100 rounded">
                <p className="font-semibold">Total: {formatCurrency(calculateTotal())}</p>
              </div>
            </div>
            
            <div className="p-4 border-t bg-gray-50 flex justify-end space-x-3">
              <button
                onClick={closeModal}
                className="px-4 py-2 border rounded hover:bg-gray-200"
              >
                Cancel
              </button>
              <button
                onClick={saveExpense}
                className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
              >
                {editMode ? 'Update' : 'Add'} Expense
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ExpenseEditor;